.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 350px;
}

.invitation {

  &__title {
    color: black;
  }

  &__button {
    height: 100px;
    width: 100%;
    font-size: large;
    background-color: aliceblue;
    border: none;
    border-radius:  50px;
    box-shadow: 10px 11px 7px 0px rgba(101,137,199,1);
    transition: background-color 0.3s linear,
      box-shadow 0.1s linear,
      color 0.3s linear;

    margin-bottom: 20px;
  }

  &__button:hover {
    cursor: pointer;
    color: white;
    background-color: rgb(57, 93, 155);
  }

  &__button:active {
    border: none;
    background-color: rgb(57, 93, 155);
    box-shadow: -7px -15px 22px -11px rgba(101,137,199,1);
  }
}

